body {
  background-color: gray;
  background-position: center;
}

#outside {
  /*border: 2px solid white;*/
}

#quote-box,
#background {
  color: white;
}

p {
  color: white;
  font-weight: bold;
  font-size: 40px;
}

#background {
  position: relative;

  /*border: 2px solid white;*/
}

#quote-box {
  margin: auto;
  padding: 280px;
  text-align: center;
  vertical-align: middle;
  font-family: "Courgette";
  text-shadow: 1px 1px black;
  /*border: 3px solid white;*/
}

.buttons {
  position: relative;
  justify-content: space-evenly;
}

#quote-box > span {
  padding: 0 10px;
}

#tweet-quote {
  color: white;
  text-decoration: none;
}

.buttons:hover {
  border: 1px ridge whitesmoke;
}
